import React, {useState} from 'react';
import classes from './App.module.css';
import RussiaCovidApp from './RussiaCovid/RussiaCovidApp';
import {Nav} from 'react-bootstrap'
import WorldCovidApp from './WorldCovid/WorldCovidApp';

function App() {
  const [world, setWorld] = useState(false);
  return (
    <div className={classes.App}>
      <header className={classes.header}>
        {/* <h2>   Covid-19  </h2> */}
        <Nav justify variant='tabs' onSelect={(selectedKey) => setWorld(selectedKey === "World")}>
        <Nav.Item/>
        <Nav.Item>
          <Nav.Link eventKey="Russia"> </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="World"> </Nav.Link>
        </Nav.Item>
        <Nav.Item/>
      </Nav>
      </header>
      
      <main className={classes.main}>
        {world 
        ?<WorldCovidApp/>
        :<RussiaCovidApp/>}
      </main>
    </div>
  );
}

export default App;
