import React, {useState} from 'react'
import axios from 'axios'
import GraphAggregator from '../GraphAggregator';
import classes from './RussiaCovidApp.module.css'
import {Dropdown, DropdownButton, Button} from 'react-bootstrap';
import {createPeriod, currentDate, randomInteger, getData, colors, periods} from '../dataApi'
import GraphItem from '../GraphItem';

const AllCases = '  '
const NewVacations = ' '
const AllVacations = '  '
const FullyVaccinated = '  '
const NewCases = '  '
const LethalCases = ' '
const RecoveryCases = ' '
const Diff = '   '

const OperationalData = [AllCases, RecoveryCases, LethalCases, FullyVaccinated]

const requests = {
  '  ': '/all_cases',
  '  ': '/new_cases',
  ' ': '/lethal_cases',
  ' ': '/recovery_cases',
  ' ': '/new_vacations',
  '  ': '/all_vacations',
  '  ': '/fully_vaccinated'
}

const options = {
    responsive: true,
    scales: {
      xAxis: {
        reverse: true
      }
    },
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: false,
        text: 'Covid-19 in Russia',
      },
    },
};
const RussiaCovidApp = () => {
    const [period, setPeriod] = useState('1')
    const [labels, setLabels] = useState(createPeriod(period))
    const [customPeriod, setCustomPeriod] = useState({
        start: new Date(),
        finish: new Date(currentDate.getFullYear(), currentDate.getMonth(), currentDate.getDate() - 14)
      })

    const dateHandler = (period, labels) => {
        switch(period){
            case '0': return labels.length + ' '
            case '1': return '14 '
            case '2': return ''
            case '3': return '3 '
            case '4': return ''
            case '5': return ' '
            default: return ' '
        }
    }

    const customPeriodHandler = event => {
        event.target.name === 'start' 
        ? setCustomPeriod({...customPeriod, start: new Date(event.target.value)}) 
        : setCustomPeriod({...customPeriod, finish: new Date(event.target.value)}) 
      }
    
      const changeCustomPeriodHandler = event => {
        const start = new Date(customPeriod.start)
        const finish = new Date(customPeriod.finish)
        setLabels(createPeriod(start, finish))        
      }

    const changePeriodHandler = eventKey => {
        const localLabels = createPeriod(eventKey)
        setPeriod(eventKey)
        setLabels(localLabels)
      }
      
    const request = async () => {
      console.log('clicked!')
      let resp = await axios({
        method: 'get',
        url: '/api/Diseaseds/New?Period=1&DateFrom=2022-05-29T12%3A28%3A07.792Z'
      })
      console.log(resp)
    }

  return (
    <div className={classes.App}>
        <div>
            <div className={classes.period}>
            <DropdownButton onSelect={changePeriodHandler} variant='primary' title=''>
                <Dropdown.Item eventKey='5' active={period === '5'}> </Dropdown.Item>
                <Dropdown.Item eventKey='1' active={period === '1'}>2 </Dropdown.Item>
                <Dropdown.Item eventKey='2' active={period === '2'}></Dropdown.Item>
                <Dropdown.Item eventKey='3' active={period === '3'}>3 </Dropdown.Item>
                <Dropdown.Item eventKey='4' active={period === '4'}>6 </Dropdown.Item>
                <Dropdown.Divider />
                <Dropdown.Item eventKey='0' active={period === '0'}> </Dropdown.Item>
            </DropdownButton>
            {
                period === '0' 
                ? <div className={classes.customPeriod}>
                     <input name='start' onChange={customPeriodHandler} type='date'/>  <input name='finish' onChange={customPeriodHandler} type='date'/> {' '} 
                    <Button style={{marginTop:'5px'}} onClick={changeCustomPeriodHandler} variant='success'>OK</Button>
                </div>
                : null
            }
            </div>
        </div>
        <div className={classes.dataGraphics}>
            {
                OperationalData.map((item, index) => (
                    <GraphItem key={index} labels={labels} label={item}><h5>{item}  {dateHandler(period, labels)}</h5></GraphItem>
                ))
            }
        </div>
        <div>
            <h2> </h2>
            <GraphAggregator country=""/>
        </div>
    </div>
  )
}

export default RussiaCovidApp